/**
 * Base styles
 */

*,
*:before,
*:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body,
.app {
  height: 100%;
  font-family: "Open Sans", sans-serif;
  color: var(--darkGrey);
}

a {
  color: var(--green);
  &:hover {
    color: var(--turquoise);
  }
}

.bold {
  font-weight: 700;
}
